import React, {useState} from "react";
import styles from "./Settings.module.css"
import user from "./../../assets/Profile/usersProfileIcon.png"
import {setNewCurrentUsersPhoto} from "../../redux/AuthReducer";
import {useDispatch, useSelector} from "react-redux";

function Settings() {
    const currentUserPhoto = useSelector(state => state.AuthPage.currentProfileImage.large)

    const [photo, setPhoto] = useState(currentUserPhoto);
    const dispatch = useDispatch();


    let ref = React.createRef();

    function getCurrentPhoto(event) {
        setPhoto(event.target.value)
    }

    return <div className={styles.SettingsContainer}>
        <div className={styles.UsersPhotoBlock}>
            <div>
                <img className={styles.usersPhoto} src={photo ? photo : user}/>
            </div>
            <div>
                <input type={"file"} ref={ref} onChange={getCurrentPhoto}/>
                <div>
                    <button onClick={() => {
                        dispatch(setNewCurrentUsersPhoto(photo));
                    }}>Update
                    </button>
                </div>
            </div>
        </div>
        <div className={styles.UsersDescriptionBlock}>
            <div>
                FullName: <input/>
            </div>
            <div>
                Status: <input/>
            </div>
            <div>
                Nickname: flofeyka <button>Change nickname</button>
            </div>
        </div>
        <div>
            <div>
                E-mail: <input/>
            </div>
            <div>
                <button>Change</button>
            </div>
            <div>
                Password: <button>Change Password</button>
            </div>
        </div>

    </div>
}

export default Settings;
